.contact{
    margin: 80px auto;
    max-width: 90%;
    display: flex;
    align-content: center;
    justify-content: space-between;
}
.contact-col{
    flex-basis: 48%;
    color: #676767;
}
.contact-col h3{
    color: #000F38;
    font-weight: 500;
    font-size: 25PX;
    display: flex;
    align-items: center;
    margin-bottom: 20px;
}
.contact-col p{
    max-width: 450px;
    list-style: 0.3;
}
.contact-col ul li {
    display: flex;
    align-items: center;
    margin: 20px 0;
  }
  
  .contact-col form input,
  .contact-col form textarea {
    display: block;
    width: 100%;
    background: #EBECFE;
    padding: 15px;
    border: 0;
    outline: 0;
    margin-bottom: 15px;
    margin-top: 5px;
    resize: none;
  }
  .contact-col span{
    display: block;
    margin: 20px 0;
  }

  /*-------------media query-------------*/

  @media(max-width:600px){
  .footer{
    display: block;
    text-align: center;
  }
  .footer ul li{
    margin: 10px;
  }
  .footer ul{
    margin-top: 10px;
  }
}