.about{
    margin: 100px auto; 
    display: flex; 
    align-items: center;
    justify-content: space-between;
    width: 90%;
}
.about-left{
    flex-basis: 40%;
    position: relative;
}
.about-right{
    flex-basis: 56%;
}
.about_img{
    width: 100%;
    border-radius: 10px;
    cursor: pointer;
}
.play_icon{
    width: 60px;
    opacity: 0.7;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    cursor: pointer;
    transition: 0.5s;
}
.play_icon:hover{
    opacity: 1;
    width: 65px;
}
.about h3{
    font-weight: 600;
    font-weight: 16px;
    color: #212EA0;
}
.about h2{
    font-size:35px;
    color: #000f38;
    margin: 10px 0;
    max-width:400px
}
.about p{
    color: #676767;
    margin-bottom: 15px;
}
/* --------------media query--------------*/
@media(max-width: 800px)
{
    .about{
        flex-direction: column;
    }
    .about-left{
        flex-basis: 100%;
        margin: 20px;
        order: 2;
    }
    .about-right{
        flex-basis: 100%;
    }
}