nav{
    width: 100%;
    display: flex;
    padding: 5px 0;
    top: 0;
    left: 0;
    display: flex ;
    align-items: center;
    z-index: 10;
    justify-content: space-between;
    position: fixed;
    background-color: rgba(255, 255, 255, 0.127);
}
.navbarLogo{
    width: 60px;
    margin-right: 10px;
}
nav ul li{
    color: rgb(0, 0, 0);
    display: inline-block;
    list-style: none;
    margin:  5px 20px;
    font-size: 16px;
    color: white;
}
.searchbox{
    display: flex;
    flex-direction: row;
}
.searchBarip {
    padding: 7px;
    font-size: 16px;
    background-color: #ffffffb2;
    color: black;
    border: none;
    border-radius: 4px 0 0 4px;
}

.searchButton {
    padding: 5px;
    background-color: #ffffffb2;
    border: none;
    border-radius: 0 4px 4px 0;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}

.searchButton img {
    width: 19px;
    height: 19px;
    background-color: #85858500;
}

.dark-nav{
    background-color: #212ea0c2;
    transition: 0.5px;
}
.menu-icon{
    display: none;
}
/* ----media Query ------*/

@media(max-width:1300px){
    .logo{
        width: 140px;
    }
    nav ul li{
        margin: 10px 15px;
    }
}
@media(max-width:1100px){
    .searchbox{
        display: none;
    }
}
@media(max-width:800px)
{
    nav{
        padding: 15px 0;
    }
    nav ul{
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        z-index: -1;
        background: #7c83bb;
        z-index: -1;
        width: 200px;
        padding-top: 70px;
        transition: 0.5s;
    }
    nav ul li{
        display: block;
        margin: 25px 40px;
        color: black;
    }
    .menu-icon{
        display: block;
        width: 30px;
        cursor: pointer;
    }
    .hide-mobile-menu{
            right: -200px;
    }
}